@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.main-wrapper {
  width: 400px;
  height: 450px;
  background-color: black;
  display: flex;
  margin: 50px auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto', sans-serif;
}
.lock-icon {
  display: flex;
  flex-direction: column;
  color: white;
}
.lock-icon img {
  width: 20px;
  height: 20px;
  background-color: pink;
  margin: 10px auto;
  padding: 5px;
  border-radius: 20px;
}
.form-name {
  margin: 0 auto;
  font-size: 22px;
  margin: 10px;
}
.input-fields {
  display: flex;
  margin: 0px auto;
  flex-direction: column;
  color: white;
}
input[type="checkbox" i] {
  margin-left: 0px;
}
input {
  background-color: black;
}
.input-text {
  color: white;
  border-radius: 5px;
  width: 300px;
  height: 40px;
  margin: 10px auto;
}
button {
  color: black;
  border-radius: 5px;
  width: 300px;
  height: 30px;
  margin: 10px auto;
  background-color: rgb(0, 174, 255);
}
.links{
    display: flex;
    width: 300px;
    justify-content: space-between;
    font-size: 12px;
    
}
a{
    color: rgb(0, 174, 255);
    text-decoration: none; 
}
.footer{
    color: #999999;
    font-size: 10px;
    margin: 50px auto 0px;
    padding-top: 30px;
}
